//FONTS

@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1),
	url("../fonts/" + $file + ".woff") format("woff"),
	url("../fonts/" + $file + ".woff2") format("woff2") {} }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//START

@font-face {
	@include fontdefault(normal, 200, 'Roboto');
	@include fontpath('Roboto-Light', 'Roboto-Light', 'Roboto-Light'); }

@font-face {
	@include fontdefault(italic, 200, 'Roboto');
	@include fontpath('Roboto-LightItalic', 'Roboto-LightItalic', 'Roboto-LightItalic'); }

@font-face {
	@include fontdefault(normal, 400, 'Roboto');
	@include fontpath('Roboto-Regular', 'Roboto-Regular', 'Roboto-Regular'); }

@font-face {
	@include fontdefault(italic, 400, 'Roboto');
	@include fontpath('Roboto-Italic', 'Roboto-Italic', 'Roboto-Italic'); }

@font-face {
	@include fontdefault(normal, 500, 'Roboto');
	@include fontpath('Roboto-Medium', 'Roboto-Medium', 'Roboto-Medium'); }

@font-face {
	@include fontdefault(italic, 500, 'Roboto');
	@include fontpath('Roboto-MediumItalic', 'Roboto-MediumItalic', 'Roboto-MediumItalic'); }

@font-face {
	@include fontdefault(normal, 700, 'Roboto');
	@include fontpath('Roboto-Bold', 'Roboto-Bold', 'Roboto-Bold'); }

@font-face {
	@include fontdefault(italic, 700, 'Roboto');
	@include fontpath('Roboto-BoldItalic', 'Roboto-BoldItalic', 'Roboto-BoldItalic'); }

@font-face {
	@include fontdefault(normal, 800, 'Roboto');
	@include fontpath('Roboto-Black', 'Roboto-Black', 'Roboto-Black'); }

@font-face {
	@include fontdefault(italic, 800, 'Roboto');
	@include fontpath('Roboto-BlackItalic', 'Roboto-BlackItalic', 'Roboto-BlackItalic'); }

@font-face {
	@include fontdefault(italic, 400, 'ComicSans');
	@include fontpath('ComicSansMS-Italic', 'ComicSansMS-Italic', 'ComicSansMS-Italic'); }
