.footer {
	background-color: $dark_gray;
	padding: 64px 0 120px;
	@include sm {
		padding: 50px 0 60px; }
	&__logo {
		text-align: center; }
	&__text-block {
		font-size: 16px;
		font-style: italic;
		color: $white;
		@include sm {
			font-size: 12px; }
		p:first-child {
			margin-bottom: 10px; }
		p:last-child {
			font-size: 14px;
			@include sm {
				font-size: 12px; } } }
	&__btn {
		box-shadow: 0 0 15px rgba(#0f10b1,.3);
		transition: fill .3s, border-color .3s, background-color .3s, box-shadow .3s;
		.icon {
			fill: $white;
			transition: fill .3s;
			transform: rotate(180deg);
			@include sm {
				width: 20px; } }
		&:hover {
			border-color: $blue;
			background: $white;
			.icon {
				fill: $blue; } } }
	.logo {
		margin-bottom: 40px;
		&__link {
			display: inline-block;
			padding: 7px 10px 1px;
			background-color: $white; } }
	.nav-menu {
		justify-content: center;
		margin-bottom: 60px;
		@include sm {
			padding: 0;
			margin-bottom: 35px; }
		&__list {
			@include sm {
				position: static;
				background-color: transparent;
				flex-direction: column;
				width: 100%;
				padding: 0; } }
		&__link {
			@include sm {
				font-size: 14px;
				color: $white; } } } }
